import React, { Component } from 'react';
import {Layout, Header, Navigation, Drawer, Content} from 'react-mdl';
import './App.css';
import Main from './components/main';
import { Link } from 'react-router-dom';

class App extends Component {
  render() {
    return (
      <div>
        <Layout className="gradient">
        <Drawer>
          <Navigation><Link onClick={ this.closeNavigation } style={{fontSize: '20px'}} to="/">Curtis Dransfield</Link></Navigation>
              <Navigation>
                <Link onClick={ this.closeNavigation } to="/experience">Experience</Link>
                <Link onClick={ this.closeNavigation } to="/aboutme">About Me</Link>
                <Link onClick={ this.closeNavigation } to="/projects">Projects</Link>
                <Link onClick={ this.closeNavigation } to="/contact">Contact Me</Link>
              </Navigation>
        </Drawer>
        <Content>
          <Header style={{color: 'white', position: 'fixed', opacity: '0.8'}} className="gradient" transparent title={<Navigation><Link style={{fontSize: '20px'}} to="/">Curtis Dransfield</Link></Navigation>}>
              <Navigation className="navigation">
                <Link to="/experience">Experience</Link>
                <Link to="/aboutme">About Me</Link> 
                <Link to="/projects">Projects</Link>
                <Link to="/contact">Contact Me</Link>
              </Navigation>
          </Header>
            <div className="spacer" />
            <div className="gradient page-content"/>
            <Main />
        </Content>
        </Layout>
      </div>
      
    );
  }

  closeNavigation(){
    document.querySelector('.mdl-layout') && document.querySelector('.mdl-layout').MaterialLayout.toggleDrawer()
  }
}

export default App;
